const cartas_guitarra = ['e1','e17','e36','i1','i10','i19','i24','i26','i32','i35','i4','i45','i6','l5','r25','r26','r45','r46','r65','r83','r99','rstar g1113','rstar g1121','rstar g1123','rstar g1131','rstar g1133','rstar g1311','rstar g1313','rstar g1321','rstar g1323','rstar g1331','rstar g1333','rstar g2111','rstar g2113','rstar g2121','rstar g2123','rstar g2131','rstar g2133','rstar g2311','rstar g2313','rstar g2321','rstar g2323','rstar g2331','rstar g2333','rstar g3111','rstar g3113','rstar g3121','rstar g3123','rstar g3131','rstar g3133','rstar g3311','rstar g3313','rstar g3321','rstar g3323','rstar g3331','rstar g3333','s18','s19','s26','s26a','s3','s4'];
const cartas_bateria = ['e14','e25','e40','i11','i18','i2','i21','i23','i33','i34','i46','l13','r12','r14','r33','r53','r6','r73','r88','rstar d1111','rstar d1113','rstar d1121','rstar d1123','rstar d1131','rstar d1133','rstar d1311','rstar d1313','rstar d1321','rstar d1323','rstar d1331','rstar d1333','rstar d2111','rstar d2113','rstar d2121','rstar d2123','rstar d2131','rstar d2133','rstar d2311','rstar d2313','rstar d2321','rstar d2323','rstar d2331','rstar d2333','rstar d3111','rstar d3113','rstar d3121','rstar d3123','rstar d3131','rstar d3133','rstar d3311','rstar d3313','rstar d3321','rstar d3323','rstar d3331','rstar d3333','s16','s2','s25','s25a','s7'];
const cartas_piano = ['e15','e35','e7','i27','i3','i40','i7','r24','r43','r62','r81','r97','s15','s8'];
const cartas_orquesta = ['e12','e28','i13','i14','i15','i16','i17','i20','i22','i25','i28','i29','i36','i39','i43','i44','i47','r31','r58','r72','r91','s21','s22'];
const cartas_produccion = ['e20','e34','e6','i12','i31','i37','i38','i42','i9','l16','r15','r16','r44','r64','r7','r82','r98','rstar p1111','rstar p1113','rstar p1121','rstar p1123','rstar p1131','rstar p1133','rstar p1311','rstar p1313','rstar p1321','rstar p1323','rstar p1331','rstar p1333','rstar p2111','rstar p2113','rstar p2121','rstar p2123','rstar p2131','rstar p2133','rstar p2311','rstar p2313','rstar p2321','rstar p2323','rstar p2331','rstar p2333','rstar p3111','rstar p3113','rstar p3121','rstar p3123','rstar p3131','rstar p3133','rstar p3311','rstar p3313','rstar p3321','rstar p3323','rstar p3331','rstar p3333'];
const cartas_carretera = ['e11','e19','e2','e21','e27','e3','e30','e31','l11','l3','l4','l6','r10','r11','r19','r20','r36','r40','r52','r60','r78','r90','s23','t1','t2','t5','t6'];

const DEFAULT_LUCKS_STATE = {
  totalCartas: 0,
  canto: 0,
  guitarra: 0,
  bateria: 0,
  piano: 0,
  orquesta: 0,
  produccion: 0,
  carretera: 0,
};

function App() {
  const [ cargando, setCargando ] = useState(false);
  const [ username, setUsername ] = useState("jotapdiez");
  const [ lucks, setLucks ] = useState(Object.assign({}, DEFAULT_LUCKS_STATE));
  const [ error, setError ] = useState(null);
  // const { canto, setCanto } = useState(0);
  // const { guitarra, setGuitarra } = useState(0);
  // const { bateria, setBateria } = useState(0);
  // const { piano, setPiano } = useState(0);
  // const { orquesta, setOrquesta } = useState(0);
  // const { produccion, setProduccion } = useState(0);
  // const { carretera, setCarretera } = useState(0);

  const changeUsername = (e) => {
    setUsername(e.target.value);
  }

  const fetchData = async (offset) => {
    let ssc_script = new SSC('https://custr.de.ryamer.com');
    if (ssc_script !== null){
        let data = await ssc_script.find(
            "nft",
            "STARinstances",
            { account: username }, //jotapdiez | fdeflash | engi
            1000,
            offset,
            []
        );

        return data;
    }else{
        console.log('sin ssc');
        return null;
    }
  }

  const parseStats = function (data) {
      //"stats": "100,1,75,3"
      //stat=fans,luck,skill,IM
      const stats = data.split(',');
      return {fans:stats[0], luck:stats[1], skill:stats[2],IM:stats[3]};
  }

  const parseData = (data, res) => {
    // console.log("*** res= ", res);
    res.totalCartas += data.length;

    for (let i =0 ; i<data.length ; ++i){
        if (data[i].properties.class === "instrument"){
            let number = data[i].properties.type.substring(0, data[i].properties.type.indexOf(' ')).toLowerCase();
            if (number === 'RSTAR'){
                console.log('number: ', number);
            }
            // console.log("*** lucks= ", lucks);
            if (cartas_canto.includes(number)){ let stats = parseStats(data[i].properties.stats); res.canto+=parseInt(stats.luck);}
            if (cartas_guitarra.includes(number)){ let stats = parseStats(data[i].properties.stats); res.guitarra+=parseInt(stats.luck);}
            if (cartas_bateria.includes(number)){ let stats = parseStats(data[i].properties.stats); res.bateria+=parseInt(stats.luck);}
            if (cartas_piano.includes(number)){ let stats = parseStats(data[i].properties.stats); res.piano+=parseInt(stats.luck);}
            if (cartas_orquesta.includes(number)){ let stats = parseStats(data[i].properties.stats); res.orquesta+=parseInt(stats.luck);}
            if (cartas_produccion.includes(number)){ let stats = parseStats(data[i].properties.stats); res.produccion+=parseInt(stats.luck);}
            if (cartas_carretera.includes(number)){ let stats = parseStats(data[i].properties.stats); res.carretera+=parseInt(stats.luck);}
        }
    }
  }

  const calculate = () => {
    setCargando(true);
    let lucks = Object.assign({}, DEFAULT_LUCKS_STATE);
    try {
      loadAllCards(0, lucks);
      setLucks(lucks);
    } catch (e) {
      setError(e);
    }
  }
  const loadAllCards = (offset, lucks) => {
    // if (offset === 0){
    //   setLucks(Object.assign({}, DEFAULT_LUCKS_STATE));
    // }

    console.log('*** loadAllCards offset = ', offset);
    fetchData(offset).then((data)=>{
        // totalCartas += data.length;
        if (data !== null && data.length>0) {
          parseData(data, lucks);
          loadAllCards(offset + data.length, lucks);
        } else {
          setCargando(false);
        }
    });
  }

  const copyBscAddress = () => {
    // navigator.clipboard.writeText('0x1a117b64D70Dbaa6C9Fd8587d6DD09FC2973ab8e');
  }
  let renderLucks = (<div>Completar username y calcular</div>);

  // console.log('cargando=', cargando, " lucks= ", lucks);
  if (!cargando && lucks && lucks.totalCartas > 0) {
    renderLucks = (<ListGroup>
      <ListGroup.Item disabled>Total de cartas: <Badge bg="secondary">{lucks.totalCartas}</Badge></ListGroup.Item>
      <ListGroup.Item>Basic Singing Lesson canto <Badge bg="secondary">{lucks.canto}</Badge></ListGroup.Item>
      <ListGroup.Item>Guitar Lesson guitarra <Badge bg="secondary">{lucks.guitarra}</Badge></ListGroup.Item>
      <ListGroup.Item>Orchestral Lesson bateria <Badge bg="secondary">{lucks.bateria}</Badge></ListGroup.Item>
      <ListGroup.Item>Production Lesson piano <Badge bg="secondary">{lucks.piano}</Badge></ListGroup.Item>
      <ListGroup.Item>Piano Lesson orquesta <Badge bg="secondary">{lucks.orquesta}</Badge></ListGroup.Item>
      <ListGroup.Item>Drum Lesson produccion <Badge bg="secondary">{lucks.produccion}</Badge></ListGroup.Item>
      <ListGroup.Item>Road Crew Lesson carretera <Badge bg="secondary">{lucks.carretera}</Badge></ListGroup.Item>
    </ListGroup>);

  //   renderLucks = (<ul>
  //   <li>Total de cartas: {lucks.totalCartas}</li>
  //   <li>canto {lucks.canto}</li>
  //   <li>guitarra {lucks.guitarra}</li>
  //   <li>bateria {lucks.bateria}</li>
  //   <li>piano {lucks.piano}</li>
  //   <li>orquesta {lucks.orquesta}</li>
  //   <li>produccion {lucks.produccion}</li>
  //   <li>carretera {lucks.carretera}</li>
  // </ul>);
  }

  return (<>
    <Container>
      <Form>
        <Form.Group className="mb-3" controlId="formBasicUsername">
          <Form.Label>RisingStar Username</Form.Label>
          <Form.Control type="username" placeholder="Enter username" value={username} onChange={changeUsername}/>
        </Form.Group>
        <Button variant="primary" type="button"  onClick={calculate}>
          Calcular
        </Button>
      </Form>
      {cargando ? <div>Cargando cartas...</div> : renderLucks}
      {error}
      <footer>
        <span>Donaciones:</span>
        <span onClick={copyBscAddress}>0x1a117b64D70Dbaa6C9Fd8587d6DD09FC2973ab8e</span>
      </footer>
      <Navbar sticky="bottom">
        <span>Donaciones:</span>
        <span onClick={copyBscAddress}>0x1a117b64D70Dbaa6C9Fd8587d6DD09FC2973ab8e</span>
      </Navbar>
    </Container>
  </>);
}

export default App;
